<template lang="html">
    <div class="">
      <h1>Download icons</h1>
      <!-- <v-row justify-center> -->
        <div class="col-12 text-center mt-4">
          <h2 v-if="icons.length == 0"></h2>
            <v-simple-table v-if="icons.length > 0"
              :dense="dense"
              :fixed-header="fixedHeader"
               height="400px"
               class="iconTable"
            >
              <template v-slot:default>
                <thead>
                  <tr>
                    <th class="text-center">Icon</th>
                    <th class="text-center">Name</th>
                    <th class="text-center">Download</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="icon in icons" :key="icon.name">
                    <td><img :src="icon.src" class="iconList"></td>
                    <td>{{ icon.name }}</td>
                    <td><v-icon @click="downloadFile(icon)">mdi-file-download</v-icon></td>
                  </tr>
                </tbody>
              </template>
             </v-simple-table>
          </div>
      <!-- </v-row> -->
    </div>
</template>

<script>
import { replaceColor } from '@/helpers/imageEncode'

export default {
  name: 'Downloader',
  data: ()  => ({
    dense: false,
    fixedHeader: true
  }),
  computed:{
    changeColor(){
      return this.$store.state.changeColor
    },
    icons(){
      const icons = this.$store.state.img64
      for (let i of icons) {
        if (this.changeColor) {
          i.src = replaceColor(i.src, this.color)
        }
      }
      return icons
    },
    color(){
      return this.$store.state.iconColor
    },
  },
  methods:{
    downloadFile(icon){
      const encoded = icon.src.substring(26)
      const decoded = atob(encoded)
      const type = "image/svg+xml"
      if (typeof window.chrome !== 'undefined') {
      // Chrome version
        const link = document.createElement('a');
        const file = new Blob([decoded], {type: type});
        link.href = window.URL.createObjectURL(file);
        link.download = icon.name;
        link.click();
      } else if (typeof window.navigator.msSaveBlob !== 'undefined') {
          // IE version
          const file = new Blob([decoded], {type:type});
          window.navigator.msSaveBlob(file, icon.name);
      } else {
          // Firefox version
          const file = new File([decoded], icon.name, { type: 'application/force-download' });
          window.open(URL.createObjectURL(file));
      }
    }
  }
}
</script>

